import React from "react";
import "./Page3.css";
import pic1 from "../../Images/pic1.jpg";
import pic2 from "../../Images/pic2.jpg";
import pic3 from "../../Images/pic3.jpg";
const Page3 = () => {
  return (
    <div className="page3container">
      <div className="page3">
        <div className="page3info">
          <p style={{ fontSize: "calc(1vw + 1rem)", paddingBottom: "20px" }}>
            Accompany
          </p>
          <p style={{ fontSize: "16px" }}>
            with Refresh skin daily with mild, evocatively aromatic Geranium
            Leaf Body Cleanser. Follow with lightly hydrating Rind Concentrate
            Body Balm, boosted with Geranium Leaf Hydrating Body Treatment for
            enriched nourishment whenever desired.
          </p>
        </div>
        <div className="page3images">
          <div>
            <img src={pic1} alt="Error" />
          </div>
          <div>
            <img src={pic2} alt="Error" />
          </div>
          <div>
            <img src={pic3} alt="Error" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Page3;
