import React, { useState } from "react";
import "./Page2.css";
import vid1 from "../../Images/vid1.jpg";
import vid from "../../Images/vid.mp4";
import { BsPauseFill, BsPlayFill } from "react-icons/all";
const Page2 = () => {
  const [click, setClick] = useState(false);

  const handleClick = () => {
    if (click) {
      setClick(false);
    } else {
      setClick(true);
    }
  };
  const vidPlay = () => {
    const vid = document.querySelector(".myvid");
    vid.play();
  };
  const vidPause = () => {
    const vid = document.querySelector(".myvid");
    vid.pause();
  };

  return (
    <div className="page2container">
      <div className="page2vidcontainer">
        <video poster={vid1} className="myvid">
          <source src={vid} type="video/mp4" />
        </video>
        <div
          className="vidplay"
          style={{ position: "absolute", bottom: "5%", left: "3%" }}
          onClick={handleClick}
        >
          {!click ? (
            <BsPlayFill
              size={40}
              style={{ cursor: "pointer" }}
              onClick={vidPlay}
            />
          ) : (
            <BsPauseFill
              size={40}
              style={{ cursor: "pointer" }}
              onClick={vidPause}
            />
          )}
        </div>
      </div>
      <div className="page2infocontainer">
        <div className="page2info">
          <p>About Tacit</p>
          <p style={{ fontSize: "calc(1vw + 1rem)", paddingBottom: "20px" }}>
            A crisp and captivating fragrance
          </p>
          <hr />
          <p
            style={{ fontSize: "16px", lineHeight: "1.5", paddingTop: "20px" }}
          >
            Tacit was born of two key inspirations: the fresh notes of
            traditional colognes and the culture, topography and fragile
            perfumed vegetation of the Mediterranean coast. It is familiar in
            its Yuzu-inspired citrus notes, yet innovative in inclusion of Basil
            to deliver a green accord with delicate spicy clove-like undertones.
          </p>
        </div>
      </div>
    </div>
  );
};

export default Page2;
