import React, { useState } from "react";
import "./NavBar.css";
import { FaBars, FaWindowClose } from "react-icons/all";
const NavBar = () => {
  const [click, setClick] = useState(false);
  const handleClick = () => {
    if (!click) setClick(true);
    else {
      setClick(false);
    }
  };
  return (
    <div className="navmaincontainer">
      <div className="abovenav">
        Enjoy complimentary standard shipping on all orders +
      </div>
      <div className="navbarcontainer">
        <nav style={{ left: click ? "0" : "-100%" }}>
          <div className="leftnav">
            <div>
              <a href="/">Shop</a>
            </div>
            <div>
              <a href="/">Read</a>
            </div>
            <div>
              <a href="/">Stores</a>
            </div>
            <div>
              <a href="/">Search</a>
            </div>
          </div>
          <div className="rightnav">
            <div>
              <a href="/">Login</a>
            </div>
            <div>
              <a href="/">Cart</a>
            </div>
          </div>
        </nav>
        <div className="bar" onClick={handleClick}>
          {!click ? <FaBars /> : <FaWindowClose />}
        </div>
      </div>
    </div>
  );
};

export default NavBar;
